<template>
  <div class="hs-container">
    <c-header isBackNav :title="Number($route.query.TravelType) === 0 ? $t('base.BusinessTrip') : $t('base.PersonalTrip')" fixedBackPath="/home">
      <p class="c-header-right" slot="right" v-if="Number($route.query.TravelType) === 0" @click="showTravelStandard = true">{{$t('base.Policy')}}</p>
    </c-header>
    <hs-scroll ref="tableView" :data="[details]" class="tableView">
      <ul>
        <div class="slider-box">
          <hs-swiper maxType="Width" :images="banners"></hs-swiper>
        </div>
        <div class="section">
          <hs-tab v-if="canBookHotel && canBookIntlHotel"
            :selectedIndex.sync="tabActivedIndex" :options="[$t('base.China_2'), $t('base.InternationalRegion_2')]" @select="_selectTab"></hs-tab>
          <div class="address" @click="showWhere = true">
            <div class="destination">
              <p>{{tabActivedIndex === 0 ? $t('base.China_2') : $t('base.InternationalRegion_2')}}{{$t('base.Destination')}}</p>
              <p v-show="tabActivedIndex === 0">{{(hotelOrder.City && hotelOrder.City[`CityName_${language}`]) ? hotelOrder.City[`CityName_${language}`]: $t('base.PleaseChoose')}}</p>
              <p v-show="tabActivedIndex === 1">{{(interHotelOrder.City && interHotelOrder.City[`CityName_${language}`]) ? interHotelOrder.City[`CityName_${language}`] : $t('base.PleaseChoose')}}</p>
            </div>
            <div class="location" :style="{visibility: tabActivedIndex === 1 ? 'hidden' : ''}" @click.stop="_getLocation">
              <div class="logo" style="background-image: url('static/image/hotel/location_2.png')"></div>
              <p>{{$t('base.Location')}}</p>
            </div>
          </div>
          <div class="item-date" @click="showDatePicker = true">
            <div class="left">
              <p>{{$t('base.Date')}}</p>
              <div>
                <p>{{$t(beTime(new Date(checkInDate)), 'date', ['MM月dd日', 'MM dd'])}}</p>
                <span>{{getWeeks(checkInDate)}}{{$t('base.CheckIn')}}</span>
              </div>
              <div>
                <p>{{$t(beTime(new Date(checkOutDate)), 'date', ['MM月dd日', 'MM dd'])}}</p>
                <span>{{getWeeks(checkOutDate)}}{{$t('base.CheckOut')}}</span>
                <span>{{$t('base.TotalNights', 'function', getApartDayCount(checkInDate, checkOutDate))}}</span>
              </div>
            </div>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
          <div class="item-number-picker" v-if="tabActivedIndex === 1">
            <div class="number-picker" :type="language">
              <p>{{$t('base.NumberOfRoomsBooked')}}</p>
              <div class="picker">
                <div class="decrease" style="background-image: url('static/image/hotel/query/decrease.png')" @click="_calculateRoomAmount(-1)"></div>
                <p class="number">{{intlRoomAmount}}</p>
                <div class="increase" style="background-image: url('static/image/hotel/query/increase.png')" @click="_calculateRoomAmount(1)"></div>
              </div>
            </div>
            <div class="number-picker" :type="language">
              <p>{{$t('base.NumberOfGuestsPerRoom')}}</p>
              <div class="picker">
                <div class="decrease" style="background-image: url('static/image/hotel/query/decrease.png')" @click="_calculateAdultAmount(-1)"></div>
                <p class="number">{{intlAdultAmount}}</p>
                <div class="increase" style="background-image: url('static/image/hotel/query/increase.png')" @click="_calculateAdultAmount(1)"></div>
              </div>
            </div>
          </div>
          <div class="item-nation" v-if="tabActivedIndex === 1" @click="showNation = true">
            <div class="left">
              <p>{{$t('base.NationalityOfThePerson')}}</p>
              <p>{{nationalityName}}</p>
            </div>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
          <div class="item-input">
            <hs-input v-model="QueryText" :placeholder="$t('base.KeywordSearch')"></hs-input>
          </div>
          <div class="item-input" @click="_openFilterHotel" v-if="tabActivedIndex === 0">
            <hs-input class="disabled" width="calc(100% - 25px)" v-model="filterDetailsText" :placeholder="$t('base.BusinessDistrictInfrastructureBrandEtc')"></hs-input>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
          <div class="item-input" @click="_openFilterHotel" v-if="tabActivedIndex === 1">
            <hs-input class="disabled" width="calc(100% - 25px)" v-model="interFilterDetailsText" :placeholder="$t('base.Infrastructure')"></hs-input>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
          <hs-gradient-button style="margin: 20px 0" :title="$t('base.Search')" @click="_gotoQuery"></hs-gradient-button>
        </div>
        <div style="width: 100%;height: 10px;"></div>
      </ul>
    </hs-scroll>
    <hs-address-picker ref="hsAddressPicker" v-model="showWhere" :switchList="[$t('base.China_2'), $t('base.InternationalRegion_2')]" :activeIndex.sync="tabActivedIndex" :title="$t('base.SelectCity')" @select="_setCity"></hs-address-picker>
    <hs-date-picker-2
      :show.sync="showDatePicker"
      :fromDate="checkInDate"
      @update:fromDate="_updateFromDate"
      :toDate="checkOutDate"
      @update:toDate="_updateToDate"></hs-date-picker-2>
    <c-travel-standard v-model="showTravelStandard" :type="2" :details="(tabActivedIndex === 0 ? details.HotelRank : intlDetails.HotelRank)" :travelPolicy="(tabActivedIndex === 0 ? details.TravelPolicy : intlDetails.TravelPolicy)"></c-travel-standard>
    <hs-nation v-model="showNation" @select="_selectNation"></hs-nation>
    <hs-filter-hotel :show.sync="showFilterHotel" :details="filterDetails" @delegate="_delegateWithUpdateFilterDetails"></hs-filter-hotel>
    <hs-filter-hotel initSelectedKey="FacilitieInfoList" :show.sync="showInterFilterHotel" :details="interFilterDetails" @delegate="_delegateWithUpdateInterFilterDetails"></hs-filter-hotel>
  </div>
</template>
<script type="text/ecmascript-6">
// 酒店预订查询页面
import HsScroll from 'base/hs-scroll/hs-scroll'
import CHeader from 'components/c-header/c-header'
import HsSwiper from 'base/hs-swiper/hs-swiper'
import HsInput from 'base/hs-input/hs-input'
import HsTab from 'base/hs-tab/hs-tab'
import HsDatePicker2 from 'base/hs-date-picker/hs-date-picker-2'
import HsAddressPicker from 'base/hs-address-picker/hs-address-picker'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsNation from 'base/hs-nation/hs-nation'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import HsFilterHotel from 'base/hs-filter/hs-filter-hotel'
import {mapGetters, mapMutations} from 'vuex'
import {kpiHomeGethomeinitinfo, kpiHotelGetHotelIndexInitInfo, kpiHotelGetInterHotelIndexInitInfo, kpiHotelGetHotelFilterCondition, kpiHotelGetInterHotelFilterCondition} from 'api'
export default {
  created() {
    if (this.noValue(this.$route.query.TravelType)) {
      this.$router.replace('/home')
      return
    }
    this._getMenuPermissions()
    this._getHotelRight()
    this._getIntlHotelRight()
  },
  computed: {
    ...mapGetters([
      'hotelOrder',
      'interHotelOrder'
    ]),
    canBookHotel() {
      return this.MenuPermissions.indexOf(2) >= 0
    },
    canBookIntlHotel() {
      return this.MenuPermissions.indexOf(4) >= 0
    }
  },
  data() {
    return {
      // 轮播图数据
      banners: ['/static/image/hotel/query/banner_001.jpeg'],
      // tab参数
      tabActivedIndex: 0,
      /** 差旅标准 */
      showTravelStandard: false,
      details: {},
      intlDetails: {},
      /********************************************************************/
      // 地址选择器
      showWhere: false,
      /** 筛选项 */
      showFilterHotel: false,
      showInterFilterHotel: false,
      filterDetails: {},
      interFilterDetails: {},
      filterDetailsText: '',
      interFilterDetailsText: '',
      QueryText: '',
      /**********************************************************************/
      /** 房间数 每间房间的入住人数 */
      intlRoomAmount: 1,
      intlAdultAmount: 2,
      /**********************************************************************/
      /** 国籍 */
      showNation: false,
      nationalityName: '中国',
      nationalityCode: 'CN',
      /** 日期选择器 */
      showDatePicker: false,
      checkInDate: this.beTime(new Date()), // 入住日期
      checkOutDate: this.beTime(new Date().setDate(new Date().getDate() + 1)), // 离店日期
      /**********************************************************************/
      MenuPermissions: [] // 权限
    }
  },
  methods: {
    ...mapMutations([
      'SET_HOTEL_ORDER',
      'SET_INTER_HOTEL_ORDER'
    ]),
    /** 权限 */
    _getMenuPermissions() {
      kpiHomeGethomeinitinfo().then((res) => {
        this.MenuPermissions = res.ResultData.MenuPermissions
        if (!this.canBookHotel && this.canBookIntlHotel) {
          this._selectTab(1)
        } else if (!this.canBookHotel && !this.canBookIntlHotel) {
          this.$router.replace('/home')
        } else {
          this._selectTab(0)
        }
      })
    },
    /** 差旅标准 */
    _getHotelRight() {
      kpiHotelGetHotelIndexInitInfo().then((res) => {
        console.log('酒店权限查询页面：', res)
        this.details = res.ResultData
      })
    },
    _getIntlHotelRight() {
      kpiHotelGetInterHotelIndexInitInfo().then((res) => {
        console.log('国际酒店权限查询页面：', res)
        this.intlDetails = res.ResultData
      })
    },
    /***************************************************************************************/
    /** 切换tab回调 */
    _selectTab(val) {
      this.tabActivedIndex = val
      this._readlocalStorageWithQuery()
    },
    /** 获取当前位置 */
    _getLocation() {
      this.$refs.hsAddressPicker.getLocation()
    },
    /** 选择城市 */
    _setCity(val) {
      if (this.tabActivedIndex === 0) {
        let res = {...this.hotelOrder}
        res.City = val
        res.CityName = val[`CityName_${this.language}`]
        res.CityID = val.CityCode
        this.SET_HOTEL_ORDER(res)
      } else {
        let res = {...this.interHotelOrder}
        res.City = val
        res.CityName = val[`CityName_${this.language}`]
        res.CityID = val.CityCode
        this.SET_INTER_HOTEL_ORDER(res)
      }
      this._getFilterCondition()
    },
    /** 筛选项 */
    _getFilterCondition() {
      if (this.tabActivedIndex === 0) {
        kpiHotelGetHotelFilterCondition({cityID: this.hotelOrder.CityID}).then((res) => {
          console.log('获取城市下的酒店筛选条件: ', res)
          this.filterDetails = res.ResultData
          for (let key of Object.keys(this.filterDetails)) {
            this.filterDetails[key].forEach((element, index) => {
              this.$set(this.filterDetails[key][index], 'selected', false)
            })
          }
        })
      } else {
        kpiHotelGetInterHotelFilterCondition({cityID: this.interHotelOrder.CityID}).then((res) => {
          console.log('获取国际城市下的酒店筛选条件: ', res)
          this.interFilterDetails.FacilitieInfoList = res.ResultData.Facilities
          for (let key of Object.keys(this.interFilterDetails)) {
            this.interFilterDetails[key].forEach((element, index) => {
              this.$set(this.interFilterDetails[key][index], 'selected', false)
            })
          }
        })
      }
    },
    _delegateWithUpdateFilterDetails(val) {
      console.log('筛选回调: ', val)
      this.filterDetails = val
      let list = []
      for (let key of Object.keys(this.filterDetails)) {
        this.filterDetails[key].forEach((element, index) => {
          element.selected && list.push(element.Name)
        })
      }
      this.filterDetailsText = list.join('、')
    },
    _delegateWithUpdateInterFilterDetails(val) {
      console.log('筛选回调: ', val)
      this.interFilterDetails = val
      let list = []
      for (let key of Object.keys(this.interFilterDetails)) {
        this.interFilterDetails[key].forEach((element, index) => {
          element.selected && list.push(element.NameChn)
        })
      }
      this.interFilterDetailsText = list.join('、')
    },
    _resetFilterDetails() {
      for (let key of Object.keys(this.filterDetails)) {
        this.filterDetails[key].forEach((element, index) => {
          this.$set(this.filterDetails[key][index], 'selected', false)
        })
      }
      this.filterDetailsText = ''
      for (let key of Object.keys(this.interFilterDetails)) {
        this.interFilterDetails[key].forEach((element, index) => {
          this.$set(this.interFilterDetails[key][index], 'selected', false)
        })
      }
      this.interFilterDetailsText = ''
    },
    _openFilterHotel() {
      if (this.tabActivedIndex === 0 ? this.noValue(JSON.stringify(this.filterDetails)) : this.noValue(JSON.stringify(this.interFilterDetails))) {
        this.$toast(this.$t('TM.PleaseSelectADestination'))
        return
      }
      this.tabActivedIndex === 0 ? (this.showFilterHotel = true) : (this.showInterFilterHotel = true)
    },
    /***************************************************************************************/
    /** 选择入住日期回调 */
    _updateFromDate(val) {
      // console.log('入住: ', val)
      if (val && val < this.beTime(new Date())) {
        this.checkInDate = this.beTime(new Date())
      } else {
        this.checkInDate = val
      }
    },
    /** 选择离店日期回调 */
    _updateToDate(val) {
      // console.log('离店: ', val)
      if (val && val <= this.checkInDate) {
        this.checkOutDate = this.getNewDate(this.checkInDate, 0, 0, 1)
      } else {
        this.checkOutDate = val
      }
    },
    /** number-picker */
    _calculateRoomAmount(type) {
      if (type === -1) {
        this.intlRoomAmount = this.intlRoomAmount > 1 ? (this.intlRoomAmount - 1) : this.intlRoomAmount;
      }
      if (type === 1) {
        this.intlRoomAmount = this.intlRoomAmount < 5 ? (this.intlRoomAmount + 1) : this.intlRoomAmount;
      }
    },
    _calculateAdultAmount(type) {
      if (type === -1) {
        this.intlAdultAmount = this.intlAdultAmount > 1 ? (this.intlAdultAmount - 1) : this.intlAdultAmount;
      }
      if (type === 1) {
        this.intlAdultAmount = this.intlAdultAmount < 5 ? (this.intlAdultAmount + 1) : this.intlAdultAmount;
      }
    },
    /***************************************************************************************/
    /** 国籍 */
    _selectNation(item) {
      console.log(item)
      this.nationalityName = item[`NationName_${this.language}`]
      this.nationalityCode = item.NationCode
    },
    /** 查询 */
    _gotoQuery() {
      if (this.tabActivedIndex === 0) {
        let res = {...this.hotelOrder}
        res.TravelType = this.$route.query.TravelType
        res.CheckInDate = this.checkInDate
        res.CheckOutDate = this.checkOutDate
        this.SET_HOTEL_ORDER(res)
        console.log(this.hotelOrder)
        if (this.noValue(this.hotelOrder.CityID)) {
          this.$toast(this.$t('TM.PleaseSelectADestination'))
          return
        }
        this.$router.push({name: 'hotelInforList', query: {QueryText: this.QueryText}, params: {filterDetails: this.filterDetails}})
      } else {
        let res = {...this.interHotelOrder}
        res.TravelType = this.$route.query.TravelType
        res.CheckInDate = this.checkInDate
        res.CheckOutDate = this.checkOutDate
        res.RoomAmount = this.intlRoomAmount
        res.AdultAmount = this.intlAdultAmount
        res.NationalityName = this.nationalityName
        res.NationalityCode = this.nationalityCode
        this.SET_INTER_HOTEL_ORDER(res)
        console.log(this.interHotelOrder)
        if (this.noValue(this.interHotelOrder.CityID)) {
          this.$toast(this.$t('TM.PleaseSelectADestination'))
          return
        }
        this.$router.push({name: 'interHotelInforList', query: {QueryText: this.QueryText}, params: {filterDetails: this.interFilterDetails}})
      }

      this._savelocalStorageWithQuery()
    },

    /** 读写localStorage内的已选项目 */
    _savelocalStorageWithQuery() {
      // 国内
      if (this.tabActivedIndex === 0) {
        window.localStorage.HISTORY_QUERY_DOMESTIC_HOTEL = JSON.stringify({
          City: this.hotelOrder.City,
          CheckInDate: this.checkInDate,
          CheckOutDate: this.checkOutDate
        })

      // 国际
      } else {
        window.localStorage.HISTORY_QUERY_INTL_HOTEL = JSON.stringify({
          City: this.interHotelOrder.City,
          CheckInDate: this.checkInDate,
          CheckOutDate: this.checkOutDate
        })
      }
    },
    _readlocalStorageWithQuery() {
      if (this.tabActivedIndex === 0 && window.localStorage.HISTORY_QUERY_DOMESTIC_HOTEL) {
        let val = JSON.parse(window.localStorage.HISTORY_QUERY_DOMESTIC_HOTEL)
        let res = {...this.hotelOrder}
        res.City = val.City
        res.CityName = val.City[`CityName_${this.language}`]
        res.CityID = val.City.CityCode
        this.SET_HOTEL_ORDER(res)
        this._updateFromDate(val.CheckInDate)
        this._updateToDate(val.CheckOutDate)
      }
      if (this.tabActivedIndex === 1 && window.localStorage.HISTORY_QUERY_INTL_HOTEL) {
        let val = JSON.parse(window.localStorage.HISTORY_QUERY_INTL_HOTEL)
        let res = {...this.interHotelOrder}
        res.City = val.City
        res.CityName = val.City[`CityName_${this.language}`]
        res.CityID = val.City.CityCode
        this.SET_INTER_HOTEL_ORDER(res)
        this._updateFromDate(val.CheckInDate)
        this._updateToDate(val.CheckOutDate)
      }
    }
    /*********************************************************************************************************/
  },
  components: {
    HsScroll,
    CHeader,
    HsSwiper,
    HsInput,
    HsTab,
    HsDatePicker2,
    HsAddressPicker,
    HsGradientButton,
    CTravelStandard,
    HsNation,
    HsFilterHotel
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
@import '~common/stylus/mixin'
.hs-container
  width 100%
  height 100%
  .c-head-right
    font-size 0.9rem
    color $color_black_text_0
  >.tableView
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .slider-box
      width 100%
      height 120px
      overflow hidden
    .section
      width calc(100% - 20px)
      margin-left 10px
      background-color $color_white
      border-radius 5px
      border 1px solid $color_gray_layer
      padding 5px 10px
      margin-top 10px
      >.address
        @extend .flex-row
        @extend .flex-between
        width 100%
        height 70px
        border-bottom 1px solid $color_gray_layer
        >.destination
          @extend .flex-column
          @extend .flex-between
          @extend .flex-top
          width calc(100% - 100px)
          height 44px
          >p
            &:nth-child(1)
              font-size 0.8rem
              color $color_gray_text
            &:nth-child(2)
            &:nth-child(3)
              font-size 1.5rem
              color $color_black_text_0
              font-weight bold
        >.location
          @extend .flex-column
          @extend .flex-between
          width 100px
          height 40px
          font-size 0.8rem
          color $color_gray_text
          border-left 1px solid $color_gray_layer
          >.logo
            width 24px
            height 24px
            background-size 100% 100%
      >.item-date
        @extend .flex-row
        @extend .flex-between
        width 100%
        padding 15px 0
        border-bottom 1px solid $color_gray_layer
        >.left
          width calc(100% - 25px)
          >p
            width 100%
            &:nth-child(1)
              font-size 0.8rem
              color $color_gray_text
              margin-bottom 10px
          >div
            @extend .flex-row
            @extend .flex-start
            width 100%
            font-size 1.1rem
            color $color_black_text_0
            margin-top 5px
            >p
              min-width 80px
              font-weight bold
            >span
              font-size 0.8rem
              color $color_gray_text
              margin-left 5px
        >.arrow
          width 20px
          height 20px
          background-size 100% 100%
      >.item-input
        @extend .flex-row
        @extend .flex-between
        width 100%
        height 70px
        border-bottom 1px solid $color_gray_layer
        >.arrow
          width 20px
          height 20px
          background-size 100% 100%
      >.item-number-picker
        @extend .flex-row
        @extend .flex-between
        width 100%
        padding 15px 0
        border-bottom 1px solid $color_gray_layer
        >.number-picker
          @extend .flex-column
          @extend .flex-between
          @extend .flex-top
          width 50%
          >p
            font-size 0.8rem
            color $color_gray_text
            margin-bottom 10px
          >.picker
            @extend .flex-row
            @extend .flex-center
            >.decrease
            >.increase
              width 20px
              height 20px
              background-size 100% 100%
            >.number
              @extend .flex-center
              width 50px
              font-size 1.1rem
              color $color_black_text_0
              font-weight bold
          &:first-child
            padding-right 10px
          &:last-child
            padding-left 10px
            border-left 1px solid $color_gray_layer
        >.number-picker[type="EN"]
          >p
            height 2em
      >.item-nation
        @extend .flex-row
        @extend .flex-between
        width 100%
        height 70px
        border-bottom 1px solid $color_gray_layer
        >.left
          @extend .flex-column
          @extend .flex-center
          @extend .flex-top
          width calc(100% - 25px)
          >p
            width 100%
            &:nth-child(1)
              font-size 0.8rem
              color $color_gray_text
              margin-bottom 10px
            &:nth-child(2)
              font-size 1.1rem
              color $color_black_text_0
              font-weight bold
        >.arrow
          width 20px
          height 20px
          background-size 100% 100%
</style>



